.app-wrapper{
  display: grid;
  margin: 0 auto;
  width: 1200px;
  grid-template-areas: 
    "header header "
    "nav content";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  /* grid-gap: 2vw; */
}
.app-wrapper-content{
  grid-area: content;
  background-color: #d2c3c3;
}